import {
    Byte1ToInt,
    Byte2ToInt,
    Byte4ToInt,
    concatenate,
    intToByte4,
    intToOneByteArr,
    intToTwoByteArr,
    processBufferRange,
    processBufferString,
    stringToUint8Array
} from "@/utils/byte";
import TokenManager from "@/utils/token_manager";
import myVue from "@/main";

require('@/protobuf/pb/gateway_pb.js');
require('@/protobuf/pb/seed_pb.js');

export const CONTENT_TYPE_ENUM = {
    JSON: 1, PROTOBUF: 2
}

const WSS_URL = `ws://${process.env.VUE_APP_WEBSOCKET_HOST}${process.env.VUE_APP_WEBSOCKET_URI}`
const CONTENT_TYPE = CONTENT_TYPE_ENUM.PROTOBUF

let setIntervalWebsocketPush = null

export default class websocket {

    constructor(server) {
        // setTimeout(function () {
        //     myVue.$notify.info({
        //         title: '通知',
        //         content: "邀请你加入视频通话",
        //         duration: 0,
        //         footer: () => {
        //             return <div slot="footer" className="t-notification__detail">
        //                 <t-button className="t-notification__detail-item t-is-active" style="user-select:none;"
        //                           onClick={() => {
        //                               window.location.href = "/video?source=notify"
        //                           }}>接受
        //                 </t-button>
        //                 <t-button className="t-notification__detail-item" style="user-select:none;color:red;"
        //                           onClick={() => {
        //
        //                           }}>拒绝
        //                 </t-button>
        //             </div>
        //         }
        //     })
        //
        // }, 500)
        this.server = server;
        this.Socket = null;
        this.token = TokenManager.Get();
        this.backFunc = {};
        this.createSocket();
    }

    async initActionMap() {
        await this.server.requestWithMethod("GET", "/gateway/action/map").then((res) => {
            this.actionMap = res.data
        })
    }

    createSocket() {
        if (!this.Socket) {
            this.initActionMap().then(() => {
                console.log('建立websocket连接')
                this.Socket = new WebSocket(WSS_URL)
                this.onopenWS();
                this.onmessageWS();
                this.onerrorWS();
                this.oncloseWS();
            })
        } else {
            console.log('已创建websocket')
        }
    }

    /**打开WS之后发送心跳 */
    onopenWS() {
        this.Socket.onopen = () => {
            // 发送验证
            if (this.token && typeof (this.token) != "undefined") {
                let loginObj = new proto.pb.Login()
                loginObj.setToken(this.token)
                this.Socket.send(this.packageMsg("CS_Login", loginObj))
            }
            // 开始发送心跳
            this.sendPing()
        }
    }

    /**连接失败重连 */
    onerrorWS() {
        this.Socket.onerror = () => {
            console.log('连接失败 发送重连')
            clearInterval(setIntervalWebsocketPush);
            this.Socket.close()
            this.createSocket() //重连
        }
    }

    /**WS数据接收统一处理 */
    onmessageWS() {
        this.Socket.onmessage = (res) => {
            console.log("websocket接受到消息", res);
            if (res.data) {
                this.parseMsg(res.data)
            } else {
                console.log('服务端返回的错误状态码')
            }
        }
    }

    /**
     * 发送数据
     * readyState = 3  连接已经关闭或者根本没有建立
     * readyState = 2  连接正在进行关闭握手，即将关闭
     * readyState = 1  连接成功建立，可以进行通信
     * readyState = 0  正在建立连接，连接还没有完成
     * @param action
     * @param content
     */
    sendWSPush(action, content) {
        if (this.Socket !== null && this.Socket.readyState === 3) {
            this.Socket.close()
            this.createSocket() //重连
        } else if (this.Socket.readyState === 1) {
            console.log("发送websocket数据 action：", action);
            this.Socket.send(this.packageMsg(action, content))
        } else if (this.Socket.readyState === 0) {
            setTimeout(() => {
                this.Socket.send(content)
            }, 5000)
        }
    }

    /**
     * 注册回调函数
     */
    registerMsgBak(action, func) {
        this.backFunc[action] = func
    }


    /**关闭WS */
    oncloseWS() {
        this.Socket.onclose = () => {
            clearInterval(setIntervalWebsocketPush)
            console.log('websocket已断开')
        }
    }

    /**发送心跳 */
    sendPing() {
        this.execPing()
        setIntervalWebsocketPush = setInterval(() => {
            this.execPing()
        }, 1000 * 10)
    }

    execPing() {
        let time = Date.now()
        console.log("心跳检测, 当前时间", time);
        let pingObj = new proto.pb.Ping()
        pingObj.setAddTime(time)
        this.Socket.send(this.packageMsg("CS_Ping", pingObj))
    }

    //解析C端发送的数据，这一层，对于用户层的content数据不做处理
    //1-4字节：当前包数据总长度，~可用于：TCP粘包的情况
    //5字节：content type
    //6字节：protocol type
    //7字节 :服务Id
    //8-9字节 :函数Id
    //10-19：预留，还没想好，可以存sessionId，也可以换成UID
    //19 以后为内容体
    //结尾会添加一个字节：\f ,可用于 TCP 粘包 分隔
    /** 打包msg **/
    packageMsg(action, contentObj) {
        let id = this.getActionId(action, "client");
        console.log("id", id);
        let serviceId = id.toString().substring(0, 2);
        let funcId = id.toString().substring(2);
        let session = "1234567890"
        console.log("serviceId:", serviceId, "funcId:", funcId, parseInt(funcId));
        let content = ""
        let protocolType = 2;
        if (CONTENT_TYPE === CONTENT_TYPE_ENUM.JSON) {
            content = contentObj.toObject()
            content = JSON.stringify(content);
            content = stringToUint8Array(content);
        } else if (CONTENT_TYPE === CONTENT_TYPE_ENUM.PROTOBUF) {
            content = contentObj.serializeBinary();
        }
        let contentLenByte = intToByte4(content.length);
        let contentTypeByte = intToOneByteArr(CONTENT_TYPE);
        let protocolTypeByte = intToOneByteArr(protocolType);
        let serviceIdByte = intToOneByteArr(parseInt(serviceId));
        let funcIdByte = intToTwoByteArr(parseInt(funcId));
        let sessionByte = stringToUint8Array(session);
        let endStr = new Uint8Array(1);
        endStr[0] = "\f";
        return concatenate(contentLenByte, contentTypeByte, protocolTypeByte, serviceIdByte, funcIdByte, sessionByte, content, endStr);
    }

    parseMsg(data) {
        let msgObj = new proto.pb.Msg().toObject();
        let that = this
        let reader = new FileReader();
        reader.readAsArrayBuffer(data);
        reader.onloadend = function (e) {
            let dataBuffer = new Uint8Array(reader.result);
            let bytes4 = processBufferRange(dataBuffer, 0, 4);
            msgObj.dataLength = Byte4ToInt(bytes4);
            let bytes1 = processBufferRange(dataBuffer, 4, 5);
            msgObj.contentType = Byte1ToInt(bytes1);
            bytes1 = processBufferRange(dataBuffer, 5, 6);
            msgObj.protocolType = Byte1ToInt(bytes1);
            bytes1 = processBufferRange(dataBuffer, 6, 7);
            msgObj.serviceId = Byte1ToInt(bytes1);
            let bytes2 = processBufferRange(dataBuffer, 7, 9);
            msgObj.funcId = Byte2ToInt(bytes2);
            let sessionBytes = processBufferRange(dataBuffer, 9, 19);
            msgObj.sessionId = processBufferString(sessionBytes, 0)
            let content = processBufferRange(dataBuffer, 19, 19 + msgObj.dataLength);
            msgObj.sidFid = `${msgObj.serviceId}${msgObj.funcId}`
            if (CONTENT_TYPE === CONTENT_TYPE_ENUM.JSON) {
                content = processBufferString(content, 0);
                msgObj.content = eval("(" + content + ")");
            } else if (CONTENT_TYPE === CONTENT_TYPE_ENUM.PROTOBUF) {
                msgObj.demo = that.getDemoName(msgObj.sidFid, "server")
                //拼接成最终classname
                var className = "proto.pb." + msgObj.demo;
                var responseProtoClass = eval(className);
                //将进制流转换成对象
                msgObj.content = responseProtoClass.deserializeBinary(content).toObject();
            }
            that.router(msgObj);
        };
    }

    getActionId(action, category) {
        if (this.actionMap !== undefined) {
            let data = this.actionMap[category];
            for (let key in data) {
                if (data[key].func_name === action) {
                    return data[key].id;
                }
            }
            return "";
        }
        return "";
    };

    getActionName(actionId, category) {
        let data = this.actionMap[category];
        return data[actionId].func_name;
    };

    getDemoName(actionId, category) {
        let data = this.actionMap[category];
        return data[actionId].demo;
    };

    router(msgObj) {
        let action = this.actionMap.server[msgObj.sidFid].func_name;
        let content = msgObj.content;
        console.log("router:", msgObj)
        console.log("action:", action)
        console.log("content:", content)

        if (this.backFunc[action] !== undefined) {
            this.backFunc[action]()
        }

        switch (action) {
            case "SC_InviteJoin":
                this.handleInviteJoin(content);
                break
            case "SC_Login":
                this.afterLogin(content);
                break
            case "SC_Pong":
                this.afterPing(content)
                break
            // default:
            //     return alert("action error." + action);
        }
    }

    handleInviteJoin(content) {
        this.showRTT("邀请通话", content.inviteTime, Date.now())
        myVue.$notification.info({
            title: '通知',
            content: content.nickname + ' 邀请你加入视频通话',
            closable: true,
            duration: 60 * 1000,
            footer: () => {
                return (<a-button href="/video?source=notify" type="primary">接受</a-button>)
            },
        })
    }

    afterLogin(content) {
        if (content.code === 200) {
            // 加入channel
            if (this.token && typeof (this.token) != "undefined") {
                console.log("发送加入渠道");
                let channelObj = new proto.pb.RequestJoinChannel()
                channelObj.setAddTime(Date.now())
                channelObj.setToken(this.token)
                channelObj.setChannelName(process.env.VUE_APP_APP_CHANNEL)
                this.Socket.send(this.packageMsg("CS_JoinChannel", channelObj))
            }
        }

    }

    afterPing(content) {
        // 显示RTT
        // let pingTimeout = content.serverResponseTime - content.addTime
        // let pongTimeout = nowTime - content.serverResponseTime
        this.showRTT("ping", content.addTime, Date.now())
        // console.log("总延迟", `${pingTimeout + pongTimeout}ms`);
        // console.log("请求服务器延迟", `${pingTimeout}ms`);
        // console.log("服务器返回延迟", `${pongTimeout}ms`);
    }

    showRTT(name, sendTime, receiveTime) {
        console.log(`${name}延迟：${receiveTime - sendTime}ms`);
    }
}
